.post{
    width: 500px;
    margin: 0 25px 40px 25px;
}
.postImg{
    width: 100%;
    height: 280px;
    object-fit: cover;
    border-radius: 10px;
}
.postInfo{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.postCat{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 12px;
    color: #be9656;
    line-height: 20px;
    margin: 15px;
    margin-top: 15px;
    margin-right: 10px;
    cursor: pointer;


}
.postTitle{
    font-family: sans-serif;
    font-size: 24px;
    font-weight: 500;
    margin-top: 15px;
    cursor: pointer;
}
.postDate{
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-style: italic;
    font-size: 15px;
    color: #999;
    margin-top: 15px;
}
.postDesc{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 16px;
    color: #444;
    line-height: 25px;
    margin-top: 15px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
}