.settings{
    display: flex;
}
.settingsWrapper{
    flex: 9;
}
.settingTitle{
    display: flex;
    align-items: center;
    justify-content: space-between;

}
.settingUpdateTitle{
    font-size: 50px;
    margin-bottom: 20px;
    color: lightcoral;
}
.settingDeleteTitle{
    color: tomato;
    font-size: 25px;
    cursor: pointer;
}
.settingsForm{
    display: flex;
    flex-direction: column;

}
.settingsPP{
    display: flex;
    align-items: center;
    margin: 10px 0; 
}
.settingsPP>img{
    width: 70px;
    height: 70px;
    border-radius: 20px;
    object-fit: cover;
}
.settingsPPIcon{
    width: 50px;
    height: 50px;
    border-radius: 50px;
    background-color: lightcoral;
    color: aqua;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 15px;
    cursor: pointer;
}
.settingsForm > label{
    font-size: 30px;
    margin-top: 20px;
}
.settingsForm > input{
    color: gray;
    margin: 15px 0;
    height: 30px;
    border: none;
    border-bottom: 1px solid lightskyblue;
}
.settingsSubmit{
    width: 150px;
    align-self: center;
    border: none;
    border-radius: 10px;
    color: white;
    background-color: aqua;
    padding: 10px;
    margin-top: 20px;
    align-items: center;
    justify-content: center;
    cursor: pointer;

}