.header{
    margin-top: 60px;
}
.headerTitles{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    color: #444;
}
.headerTitlesSm{
    position: absolute;
    top: 12%;
    font-size: 20px;
}
.headerTitlesLg{
    position: absolute;
    top: 15%;
    font-size: 100px;
}
.headerImg{
    width: 100%;
    height: 700px;
    margin-top: 80px;
    object-fit: cover;

}