.register{
    height:calc(100vh - 50px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: url("https://i.pinimg.com/originals/57/c7/18/57c718d5d2027c54e17ad40eb4fb1258.jpg");
    background-size: cover;
}
.registerTitle{
    font-size: 80px;
    color: white;
}
.registerForm{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
}
.registerForm > label{
    margin: 10px 0;
}
.registerForm > input{
    padding: 10px;
    background-color: white;
    border: none;

}
.registerButton{
    margin-top: 20px;
    cursor: pointer;
    background-color: rgb(57, 85, 243);
    border: none;
    color: white;
    border-radius: 10px;
    padding: 10px;
    font-size: 16px;
}
.registerRegisterButton{
    position: absolute;
    top: 60px;
    right: 20px;
    background-color: teal;
    cursor: pointer;
    border: none;
    padding: 10px 20px;
    font-size: larger;
    color: white;
    border-radius: 10px;
}