.top{
    width: 100%;
    height: 50px;
    background-color: rgb(39, 207, 245);
    top: 0;
    display: flex;
    align-items: center;
    z-index: 999;
    position: sticky;

}

.topLeft,.topRight{
    flex: 3;
    display: flex;
    align-items: center;
    justify-content: center;
}

.topCenter{
    flex: 6;
}
.topIcon{
    font-size: 40px;
    margin-right: 20px;
    color: rgb(213, 214, 232);
    cursor: pointer;
}
.topImage{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}
.topList{
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;
}
.topListItem{
    margin-right: 20px;
    font-size: 20px;
    font-weight: 400;
    cursor: pointer;
}
.SearchIcon{
    font-size: 18px;
    color: #666;
    cursor: pointer;
    font-size: 28px;
    margin-left: 15px;
}