.singlePost{
    flex: 9;
}
.singlePostWrapper{
    padding: 20px;
    padding-right: 0;
}
.singlePostImg{
    width: 100%;
    border-radius: 5px;
    object-fit: cover;
}
.singlePostTitle{
    text-align: center;
    margin: 10px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 28px;

}
.singlePostEdit{
    float: right;
}
.singlePostIcon{
    margin-left: 15px;
    cursor: pointer;
}
.singlePostIcon:first-child{
    color: indigo;
}
.singlePostIcon:last-child{
    color: rgb(244, 58, 58);
}
.singlePostInfo{
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    font-size: 22px;
    color: rgba(0, 0, 0, 0.481);
}
.singlePostDesc{
    color: #666;
    font-size: 22px;
    line-height: 25px;
}
.singlePostDesc::first-letter{
    margin-left: 20px;
    font-size: 30px;
    font-weight: 600;


}